import noData from '@/components/noData'
import back from '@/components/back'
export default {
	components: {
		noData,
		back
	},
	data() {
		return {
			search: '',
			show3: false,
			inputFlag: false,
			page: 1,
			list: null,
			haveMore: true,
			temp: false,
			placeholder: "输入关键字",
			isFirstIn:true,
			scrollTopDistance:0,
		}
	},
	created() {
		//判断是否有该机构信息
		this.pull(false)
	},
	mounted() {
		document.title = this.$route.meta.title
		this.getMore()
		this.reload()
	},
	beforeDestroy() {
		window.onscroll = null
	},
	beforeRouteLeave(to, from, next) {
		window.onscroll = null
		if(from.meta.keepAlive) {　
			from.meta.scollTopPosition = this.scrollTopDistance;
		}
		next()
	},
	beforeRouteEnter(to, from, next) { //进入前    	
		next(vm => {
			vm.getMore()
			vm.reload()
			let flag = true
			if(from.path == "/specialist-intro") {
				flag = false
			}
			if(vm.isFirstIn) {
				vm.isFirstIn = false
			}
			if(flag || (from.path != vm.$store.state.prevUrl && vm.$store.state.prevUrl != null) || vm.$store.state.isFresh) {
				vm.$store.dispatch('pushIsFresh', false)
				vm.search = ""
				vm.show3 = false
				vm.inputFlag = false
				vm.page = 1
				vm.list = null
				vm.haveMore = true
				vm.temp = false
				vm.pull(false)
			} else {
				setTimeout(() => {
					vm.$refs.el3.scrollTop = to.meta.scollTopPosition;   
				}, 1)
			}
		});
	},
	methods: {
		searchFor() {
			if(this.inputFlag) {
				return
			}
			this.page = 1
			this.list = null
			this.temp = false
			this.pull(false)
		},
		checkDetails(i) {
			this.$router.push({
				path: "/specialist-intro",
				query: {
					id: i.SpecialistHosGroupId,
				}
			})
		},
		pull(h) {
			this.$post("/PlatFormAPI/SpecialistHosGroup/QueryAllDrSpecialistHosGroupForPage",{
				HosGroupName:this.search,
				DrId:this.$route.query.drid,//医生Id(-1查询所有)
				IsDisplay:0,//是否不显示(0-否-显示，1-是-不显示),null全部
				IsMDT:1,//是否开启MDT(0-否-，1-是),null全部
				PageIndex: this.page,
				PageSize: 10,
				SortField:"DisplayOrder",
				SortDirection:false,
			}).then(o => {
				this.show3 = false
				if(!o.ReturnData || o.ReturnData.length < 10) {
					this.haveMore = false
				} else {
					this.haveMore = true
				}
				if(h) {
					o.ReturnData = o.ReturnData || []
					o.ReturnData.forEach(v => {
						this.list.push(v)
					})
					this.temp = true
				} else {
					this.list = o.ReturnData || []
					this.temp = true
				}
			})
		},
		getMore() {
			/*const el = this.$refs.el
			let timer = null
			window.onscroll = () => {
				clearTimeout(timer)
				timer = setTimeout(() => {
					const top = document.documentElement.scrollTop ||
						window.pageYOffset ||
						document.body.scrollTop,
						h = el.scrollHeight,
						wh = window.innerHeight
					if(h - top - wh < 100 && this.haveMore && this.temp) {
						this.temp = false
						this.page += 1
						this.pull(true)
					}
				}, 100)
			}*/
			const el3 = this.$refs.el3
            let timer3 = null
            el3.onscroll = () => {
                clearTimeout(timer3)
                timer3 = setTimeout(() => {
                   const top = el3.scrollTop
                                || el3.pageYOffset
                                || el3.scrollTop,
                            h = el3.scrollHeight,
                            wh = window.innerHeight
                    this.scrollTopDistance = top;
                }, 100)
            }
		},
		reload(){
            const el2 = this.$refs.el3
            let startX = null
            let startY = null
            let endX = null
            let endY = null
            el2.addEventListener('touchstart', e => {
                try {
			      	var _touch = e.touches[0],
			        x = Number(_touch.pageX),
			        y = Number(_touch.pageY);
			      	startX = x;
			      	startY = y;
			    } catch (e) {
			      	alert(e);
			    }
            })
            el2.addEventListener('touchmove', e => {
                let point = e.touches[0];
				let eleTop = el2.scrollTop;
				let eleScrollHeight = el2.scrollHeight;
				let eleOffsetHeight = el2.offsetHeight;
				let eleTouchBottom = eleScrollHeight - eleOffsetHeight;
				if(eleTop === 0) {
					if(point.clientY > startY) {
						e.preventDefault();
					}
					if(point.clientY - startY > 30){
						this.show3 = true
					}
				} else if(eleTop === eleTouchBottom) {
					if(point.clientY < startY) {
						e.preventDefault()
					}
				}
				if (eleScrollHeight - eleTop - eleOffsetHeight < 100 && this.haveMore && this.temp && point.clientY < startY){
					//this.showMore = true
					this.temp = false
                    this.page += 1
                    this.pull(true);
                }
            })
            el2.addEventListener('touchend',e => {
            	let point = e.changedTouches[0];
            	let eleTop = el2.scrollTop;
                if(this.show3 && eleTop == 0 && (point.clientY > startY)){
                    this.page = 1
                    this.haveMore = true
                    this.pull(false)
                }
            })
        },
		/*reload() {
			const el2 = this.$refs.el3
			let startX = null
			let startY = null
			let endX = null
			let endY = null
			el2.addEventListener('touchstart', e => {
				startX = e.changedTouches[0].pageX
				startY = e.changedTouches[0].pageY
			})
			el2.addEventListener('touchmove', e => {
				endX = e.changedTouches[0].pageX
				endY = e.changedTouches[0].pageY
				if(Math.abs(startX - endX) < Math.abs(startY - endY) && (startY - endY) < 0) {
					const top = document.documentElement.scrollTop ||
						window.pageYOffset ||
						document.body.scrollTop
					if(top == 0) {
						this.show3 = true
					}
				}
			})
			el2.addEventListener('touchend', () => {
				if(this.show3) {
					this.page = 1
					this.haveMore = true
					this.list = null
					this.pull(false)
				}
			})
		}*/
	}
}



